import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { AngularFireDatabase, FirebaseObjectObservable } from 'angularfire2/database-deprecated';
import { User } from "../../../models/user";
import { AuthData } from '../../../providers/auth-data';

@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html'
})
export class ProfilePage {

  user = {} as User;
  onEdit: boolean;
  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController, private toastCtrl: ToastController, public authData: AuthData) {

    this.onEdit = false;
    this.authData.getUserProfile().then(profiData => {

      if (this.authData.isAuth()) {
        console.log('logged');

      } else {
        console.log('not logged');
      }
      if (profiData.length >= 1) {
        this.user = profiData[0];
        console.log("this is profile", this.user);
      } else {
        console.log('error getting user');
        this.navCtrl.setRoot('MainPage');
      }
    });
  }

  edit() {

    if (this.onEdit == false) {
      this.onEdit = true;
    } else {
      this.onEdit = false;
    }
  }

  save(user) {
    console.log(user);
    this.authData.mapUserData(user).then(profiData => {
      console.log(profiData);
      this.edit();

    }, function () {
      console.log("malo");
    });


  }


  presentToast(position: string, message: string) {
    let toast = this.toastCtrl.create({
      message: message,
      position: position,
      duration: 1000
    });
    toast.present();
  }

}



